import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Popconfirm, Col, message } from 'antd';
import { getBuildings, deleteBuilding } from 'services/building';
import { TableData } from 'components/common';

const urlForm = '#buildings/form';

class DormitoryBuilding extends Component {
  state = { reload: 0 };

  columns = [
    {
      title: 'Cơ sở đào tạo',
      dataIndex: 'base',
      key: 'base',
      width: 200,
    },
    {
      title: 'Mã',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: 'Tên',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Số tầng',
      dataIndex: 'noOfFloor',
      key: 'noOfFloor',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <Fragment>
          <BtnEdit href={`${urlForm}/edit/${record.id}`} />
          <Popconfirm
            title="Bạn có muốn xóa?"
            okText="Xóa"
            cancelText="Hủy"
            onConfirm={() => this.handleDelete(record.id)}
          >
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ),
    },
  ];

  handleDelete = async id => {
    await deleteBuilding(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    return (
      <Card title="Tòa nhà">
        <Row>
          <Col span={24}>
            <BtnAddNew href={`${urlForm}/new`} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              bordered
              columns={this.columns}
              service={getBuildings}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DormitoryBuilding />
  </Page>
);



